.list-inline {
    padding-top: 2.5em;
}
.list-inline > li {
    display: inline-block;
}

.social-icon {
    padding: 5px;
}
li > a:link,
li > a:visited,
li > a:active {
    color: inherit;
}
li > a:hover {
    color: lightgray;
}

.copyright {
    color: inherit;
    margin-bottom: 2em;
}