.App {
    text-align: center;
}

.gradient {
    background-color: #212529;

    background: -webkit-linear-gradient(50deg, #1a2980, #26d0ce) !important;
    background: linear-gradient(40deg, #1a2980, #26d0ce) !important;
}

.drop-shadow {
    -webkit-filter: drop-shadow(0 5px 10px gray);
    filter: drop-shadow(0px 5px 10px gray);
}

.drop-shadow-dark {
    -webkit-filter: drop-shadow(0 5px 10px darkslategray);
    filter: drop-shadow(0px 5px 10px darkslategray);
}

section {
    position: relative;
}